<template>
  <div>
    <!-- button -->
    <div v-if="button">
      <v-btn
        :color="button.color ? button.color : 'primary'"
        :icon="button.type === 'icon'"
        @click="openDialog(defaultItem)"
      >
        <v-icon
          :color="button.iconColor ? button.iconColor : ''"
          :size="button.iconSize ? button.iconSize : ''"
        >
          {{ button.icon ? button.icon : 'mdi-plus' }}
        </v-icon>
        {{ button.text ? button.text : $t('crud.button.text') }}
      </v-btn>
    </div>
    <!-- header -->
    <v-row v-if="table" class="d-flex my-2">
      <!-- bult delete -->
      <v-col cols="6">
        <v-select
          v-if="table.showSelect"
          v-model="bulkDeleteValue"
          :disabled="selected.length === 0"
          :label="$t('crud.table.bulkActions.label')"
          outlined
          dense
          hide-details
          :items="[
            {
              text: $t('crud.table.bulkActions.deleteSelected'),
              value: 'Delete Selected'
            }
          ]"
          item-text="text"
          item-value="value"
          @change="bulkDelete"
        ></v-select>
      </v-col>

      <!-- crud button -->
      <v-col v-if="table.button" class="text-right" cols="6">
        <v-btn
          :color="table.button.color ? table.button.color : 'primary'"
          :icon="table.button.type === 'icon'"
          @click="openDialog(defaultItem)"
        >
          <v-icon
            :color="table.button.iconColor ? table.button.iconColor : ''"
            :size="table.button.iconSize ? table.button.iconSize : ''"
            >{{ table.button.icon ? table.button.icon : 'mdi-plus' }}</v-icon
          >
          {{
            table.button.text ? table.button.text : $t('crud.table.button.text')
          }}
        </v-btn>
      </v-col>
    </v-row>

    <!-- crud table -->
    <v-card v-if="table">
      <v-row align-content="center" justify="center" class="px-5">
        <v-col sm="6" cols="12">
          <v-card-title v-if="table.heading">
            {{ table.heading }}
          </v-card-title>
        </v-col>
        <v-col align-self="center" sm="6" cols="12">
          <v-text-field
            v-if="table.search"
            v-model="search"
            outlined
            hide-details
            dense
            :append-icon="table.search.icon ? table.search.icon : 'mdi-magnify'"
            :label="
              table.search.label
                ? table.search.label
                : $t('crud.table.search.label')
            "
          ></v-text-field>
        </v-col>
      </v-row>

      <!-- data table -->
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="data"
        :item-key="table.itemKey ? table.itemKey : '_id'"
        multi-sort
        :show-select="table.showSelect"
        class="elevation-1"
        :search="search"
        :loading-text="
          table.loadingText ? table.loadingText : 'data is loading ...'
        "
        :no-data-text="table.noDataText ? table.noDataText : 'no data found'"
        :no-results-text="
          table.noResultsText ? table.noResultsText : 'no results found'
        "
        :loading="loading.data"
      >
        <!-- Display all the item slots -->
        <template
          v-for="(x, slotName) in $scopedSlots"
          v-slot:[slotName]="context"
        >
          <slot :name="slotName" v-bind="context" />
        </template>

        <!-- actions template -->
        <template v-if="actions" v-slot:[`item.actions`]="{ item: i }">
          <!-- view button -->
          <v-icon
            v-if="actions.view && viewData.redirect"
            color="info"
            class="mr-2"
            @click="$router.push(viewRedirect(viewData.url, i))"
          >
            mdi-eye
          </v-icon>
          <v-icon
            v-if="actions.view && !viewData.redirect"
            color="info"
            class="mr-2"
            @click="openViewDialog(i)"
          >
            mdi-eye
          </v-icon>
          <!-- edit button -->
          <v-icon
            v-if="actions.edit && editData.redirect"
            color="success"
            class="mr-2"
            @click="$router.push(editRedirect(editData.url, i))"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            v-if="actions.edit && !editData.redirect"
            color="success"
            class="mr-2"
            @click="editDialog(i)"
          >
            mdi-pencil
          </v-icon>
          <!-- delete button -->
          <v-icon v-if="actions.delete" color="error" @click="itemDelete(i)">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>

    <!-- Add Edit Dialog -->
    <crud-add-edit-dialog
      :show="showAddEditDialog"
      :dialog="addEditDialog"
      :edit="edit"
      :loading="loading"
      @editItem="itemEdit"
      @saveItem="itemSave"
      @close="closeAddEditDialog"
    >
      <template slot="form">
        <slot name="crud-form" :item="item" :edit="edit"></slot>
      </template>
    </crud-add-edit-dialog>

    <!-- Delete Dialog -->
    <crud-delete-dialog
      :dialog="deleteDialog"
      :show="showDeleteDialog"
      :loading="loading"
      @success="success"
      @close="closeDeleteDialog"
    />

    <!-- View Dialog -->
    <crud-view-dialog
      :dialog="viewDialog"
      :show="showViewDialog"
      @close="closeViewDialog"
    >
      <template slot="view">
        <slot name="view-item" :item="item"></slot>
      </template>
    </crud-view-dialog>

    <!-- Alert Snackbar -->
    <crud-snackbar :alert="alert" />
  </div>
</template>

<script>
import crud from '@/mixins/crud'
export default {
  components: {
    CrudAddEditDialog: () => import('@/components/crud/CrudAddEditDialog'),
    CrudDeleteDialog: () => import('@/components/crud/CrudDeleteDialog'),
    CrudViewDialog: () => import('@/components/crud/CrudViewDialog'),
    CrudSnackbar: () => import('@/components/crud/CrudSnackbar')
  },
  mixins: [crud],
  props: {
    crudId: { type: [String, Boolean], default: false },
    table: { type: [Boolean, Object], default: false },
    addEditDialog: { type: [Boolean, Object], default: false },
    deleteDialog: { type: [Boolean, Object], default: false },
    viewDialog: { type: [Boolean, Object], default: false },
    status: { type: [String], default: 'add' },
    button: { type: [Boolean, Object], default: false },
    headers: { type: Array, default: () => [] },
    items: {
      type: [Boolean, Array],
      default: () => {
        return []
      }
    },
    defaultItem: {
      type: Object,
      default: () => {
        return {}
      }
    },
    fetchData: { type: [Boolean, Object], default: false },
    addData: { type: [Boolean, Object], default: false },
    editData: { type: [Boolean, Object], default: false },
    viewData: { type: [Boolean, Object], default: false },
    deleteData: { type: [Boolean, Object], default: false },
    deleteMultipleData: { type: [Boolean, Object], default: false },
    actions: { type: [Boolean, Object], default: false }
  },
  data() {
    return {
      bulkDeleteValue: '',
      selected: [],
      search: '',
      itemToDel: null,
      deleteType: '',
      showAddEditDialog: false,
      item: Object.assign({}, this.defaultItem),
      edit: this.status === 'edit',
      data: this.items,
      showDeleteDialog: false,
      showViewDialog: false
    }
  },
  // fetch items if fetch data
  async created() {
    if (this.fetchData) {
      await this.fetchItems(false)
    }
  },
  methods: {
    getEditItem(item) {
      const editItem = Object.assign({}, item)
      return editItem
    },

    closeAddEditDialog() {
      this.showAddEditDialog = false
      this.item = Object.assign({}, this.defaultItem)
    },

    closeDeleteDialog() {
      this.showDeleteDialog = false
      // change bulk delete value to default
      this.bulkDeleteValue = ''
    },

    closeViewDialog() {
      this.showViewDialog = false
    },

    // open dialog
    openDialog(item) {
      if (this.addData.redirect) {
        return this.$router.push(this.addData.url)
      }
      this.showAddEditDialog = true
      this.edit = this.status === 'edit'
      // if (this.status === 'edit') {
      this.item = Object.assign({}, item)
      // } else {
      // }
    },
    // edit dialog
    editDialog(item) {
      this.edit = true
      this.showAddEditDialog = true
      this.item = Object.assign({}, item)
    },

    openViewDialog(item) {
      this.showViewDialog = true
      this.item = Object.assign({}, item)
    },

    // item save
    async itemSave(form, itemForm) {
      if (!this.addData) {
        this.$emit('saveItem', this.item, form)
      } else {
        await this.addItem(this.item, form, itemForm)
      }
    },

    // item edit
    async itemEdit(form) {
      if (!this.editData) {
        this.$emit('editItem', this.item, form)
      } else {
        await this.editItem(this.item, form)
      }
    },

    // delete confirm success
    async success() {
      if (this.deleteType === 'single') {
        if (!this.deleteData) {
          this.$emit('deleteItem', this.itemToDel)
        } else {
          await this.deleteItem()
        }
      } else if (this.deleteType === 'multiple') {
        if (!this.deleteData) {
          this.$emit('deleteItems', this.selected)
        } else {
          await this.deleteItems(this.selected)
        }
      }
    },

    // delete single item click func
    itemDelete(item) {
      this.deleteType = 'single'
      this.itemToDel = item
      this.showDeleteDialog = true
    },

    // delete multiple items click func
    itemsDelete() {
      this.deleteType = 'multiple'
      this.showDeleteDialog = true
    },

    // edit data redirect url
    editRedirect(url, item) {
      const newUrl = url.replace(':param', item[this.editData.param])
      return newUrl
    },

    viewRedirect(url, item) {
      const newUrl = url.replace(':param', item[this.viewData.param])
      return newUrl
    },

    // bulk delete funcs
    bulkDelete() {
      if (this.bulkDeleteValue === 'Delete Selected') {
        this.itemsDelete(this.selected)
      }
    }
  }
}
</script>

<style scope>
.dialog-btn {
  position: fixed;
  right: 20px;
  bottom: 50px;
  z-index: 100;
}
</style>
